import React, {useCallback, useState} from 'react';
import styles from './page.module.css';
import { useDispatch} from "react-redux";
import {Button, EmailInput} from "@ya.praktikum/react-developer-burger-ui-components";
import {Link, useNavigate} from "react-router-dom";
import {passwordChangeRequest} from "../services/reducers/auth";

export const ForgotPasswordPage = () => {

  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const dispatch = useDispatch();

  const onChange = e => {
    setEmail(e.target.value);
  };

  const handleRegister = useCallback(
    e => {
      e.preventDefault();
      dispatch(passwordChangeRequest({"email": email}));
      navigate("/reset-password");
    },
    [dispatch, email]
  );

  return (
    <main className={styles.wrapper}>
      <form className={styles.form}>
        <h1 className="text text_type_main-medium text_color_primary"> </h1>
        <EmailInput
          onChange={onChange}
          value={email}
          placeholder={' e-mail'}
          name={'email'}
        />
        <Button onClick={handleRegister} htmlType="button" type="primary" size="medium">
          
        </Button>
      </form>
      <p className="text text_type_main-default text_color_inactive">
         ? <Link to="/login" className={styles.link}></Link>
      </p>
    </main>
  );
}
